import 'content.scss';
import '_lazyload';
import '_lightbox';
import '_header';


window.addEventListener('DOMContentLoaded', function() {
    (function($) {

        $('.link').click(function(){
            prompt('複製連結', window.location.href)
        });

        //--------------------- imagesLoaded ---------------------//
        function removeLoading() {
            $(".loading").fadeOut(function() {
                $(this).remove();
            }); 
        }

        imagesLoaded("html, body", function() {
            removeLoading();
        });

    })(jQuery);
});